import { ICONS_MANIFEST } from '@porsche-design-system/assets';
import { TYPOGRAPHY_TEXT_COLORS } from '../../utils/typography/typography-text-color';
import { getCDNBaseURL } from '../../utils';
export const ICON_ARIA_ATTRIBUTES = ['aria-label'];
export const ICON_COLORS = [...TYPOGRAPHY_TEXT_COLORS, 'state-disabled'];
export const isUrl = (str) => str?.length > 0 && /(\/)/.test(str);
const DEFAULT_ICON_NAME = 'arrow-right';
// eslint-disable-next-line @typescript-eslint/no-redundant-type-constituents
export const buildIconUrl = (iconNameOrSource = DEFAULT_ICON_NAME) => {
    if (iconNameOrSource === null) {
        return buildIconUrl(DEFAULT_ICON_NAME);
    }
    else if (isUrl(iconNameOrSource)) {
        return iconNameOrSource;
    }
    else if (ICONS_MANIFEST[iconNameOrSource]) {
        return `${getCDNBaseURL()}/icons/${ICONS_MANIFEST[iconNameOrSource]}`;
    }
    return buildIconUrl(DEFAULT_ICON_NAME);
};
